<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">关联回款</span>
				</div>
			</div>
			<div class="relete_back_title flex_between">
				<div class="" v-if="!is_log_editable">
					本次已选择待分配总额<span>{{ total_selected_money }}万元</span>
				</div>
				<div class="" v-else>
					本次已选择待分配总额<span>{{ total_selected_money_edit }}万元</span>
				</div>
				<div class="blue_btn cursor" v-if="!is_log_editable" @click="editLog">
					修改
				</div>
				<div class="flex_end cursor" v-else>
					<div class="hover_btn cursor" @click="editCancel">
						取消
					</div>
					<div class="blue_btn cursor" @click="editSaveLog">
						确定
					</div>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table ref="multipleTable" :data="relate_table_data" @selection-change="handleSelectionChange"  border style="width: 100%" >
						<el-table-column v-if="is_log_editable" type="selection" width="55">
    						</el-table-column>
    						<el-table-column prop="event" label="序号" width="100">
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column prop="receive_date" label="到账日期" width="180">
						</el-table-column>
						<el-table-column prop="event" label="事件名称" width="180">
						</el-table-column>
						<el-table-column prop="fund_name" label="标的名称" width="180">
						</el-table-column>
						<el-table-column prop="payback_type_name" label="回款类型" width="180">
						</el-table-column>
						<el-table-column prop="sum_back" label="回款返本（万元）" width="180">
						</el-table-column>
						<el-table-column prop="sum_profile" label="回款收益-税后（万元）" width="200">
						</el-table-column>
						<el-table-column prop="sum_calculate" label="回款总额（万元）" width="180">
						</el-table-column>
						<el-table-column prop="rest_fee" label="待分配余额（万元）" width="180">
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="120">
							<template slot-scope="scope">
								<el-button type="text" size="small" @click="view(scope.row)">查看</el-button>
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--分配模块开始-->
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">分配</span>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table ref="" :data="total_table_data" border style="width: 100%">
						<el-table-column prop="plan_receive_date" label="计划到账日期" width="200">
							<template slot-scope="scope">
								<div class="" v-if="is_total_editable">
									<el-date-picker v-model="scope.row.plan_receive_date" @change="chooseDate($event)" :editable='false' type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
									</el-date-picker>
								</div>
								<div class="" v-else>
									{{ scope.row.plan_receive_date }}
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="event" label="事件" width="200">
						</el-table-column>
						<el-table-column prop="scale" label="本金占比" width="200">
							<template slot-scope="scope">
								<div class="flex_between" v-if="is_total_editable">
									<el-input v-model="scope.row.scale" placeholder="本金占比" clearable>
									</el-input>
									%
								</div>
								<div class="" v-else>
									{{ scope.row.scale }}&nbsp;%
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="sum_distribution" label="分配本金（万元）" width="200">
						</el-table-column>
						<el-table-column prop="sum_distribution_before_tax" label="分配收益-税前（万元）" width="200">
						</el-table-column>
						<el-table-column prop="distributions" label="分配总额（万元）" width="200">
						</el-table-column>
						<el-table-column prop="unit" label="币种" width="200">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="120">
							<template slot-scope="scope">
								<div class="" v-if="!is_total_editable">
									<el-button type="text" size="small" @click="editTotal(scope.row)">编辑</el-button>
								</div>
								<div class="" v-else>
									<el-button type="text" size="small" @click="editSaveTotal(scope.row)">保存</el-button>
									<el-button type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
								</div>
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--明细模块开始-->
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">分配明细</span>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table ref="" :data="detail_table_data" border style="width: 100%">
						<el-table-column prop="event" label="序号" width="100" sortable>
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column prop="investor_name" label="合伙人名称" width="200" sortable>
						</el-table-column>
						<el-table-column prop="subscribe" label="实缴出资（万元）" width="200" sortable>
						</el-table-column>
						<el-table-column prop="subscribe_scale" label="实缴占比（%）" width="200" sortable>
							<template slot-scope="scope">
								{{ scope.row.subscribe_scale }}&nbsp;%
							</template>
						</el-table-column>
						<el-table-column prop="receive_date" label="实际到账日期" width="200" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.receive_date }}
								</div>
								<div class="" v-else>
									<el-date-picker v-model="scope.row.receive_date" type="date" :editable='false'  value-format="yyyy-MM-dd" placeholder="选择日期">
									</el-date-picker>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="principal_distribution" label="本金分配（万元）" width="200" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.principal_distribution }}
								</div>
								<div class="" v-else>
									<el-input v-model.number="scope.row.principal_distribution" v-on:input="calculate(scope.$index)" placeholder="请输入" clearable>
									</el-input>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="income_before_tax" label="收益分配-税前（万元）" width="200" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.income_before_tax }}
								</div>
								<div class="" v-else>
									<el-input v-model.number="scope.row.income_before_tax" placeholder="" clearable>
									</el-input>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="interest" label="应付利息/利息收入（万元）" width="230" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.interest }}
								</div>
								<div class="" v-else>
									<el-input v-model="scope.row.interest" placeholder="" clearable>
									</el-input>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="income_after_interest" label="息后收益分配-税前（万元）" width="230" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.income_after_interest }}
								</div>
								<div class="" v-else>
									<el-input v-model="scope.row.income_after_interest" placeholder="" clearable>
									</el-input>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="service_fee" label="代扣代缴（20%）" width="200" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.service_fee }}
								</div>
								<div class="" v-else>
									<el-input v-model="scope.row.service_fee" placeholder="" clearable>
									</el-input>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="final_income" label="息后税后收益分配（万元）" width="230" sortable>
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.final_income }}
								</div>
								<div class="" v-else>
									<el-input v-model="scope.row.final_income" v-on:input="calculate(scope.$index)" placeholder="" clearable>
									</el-input>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="this_sum_distribution" label="本次分配总额（万元）" width="200" sortable>
							
						</el-table-column>
						<el-table-column prop="last_sum_distribution" label="上次累计分配总额（万元）" width="230" sortable>
							<!--<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									{{ scope.row.last_sum_distribution }}
								</div>
								<div class="" v-else>
									<el-input v-model="scope.row.last_sum_distribution" placeholder="" clearable>
									</el-input>
								</div>
							</template>-->
						</el-table-column>
						<el-table-column prop="sum_distribution" label="累计分配金额（万元）" width="200" sortable>
							
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="120">
							<template slot-scope="scope">
								<div class="" v-if="currentIndex != scope.row.id">
									<el-button type="text" size="small" @click="editDetail(scope.row)">编辑</el-button>
								</div>
								<div class="" v-else>
									<el-button type="text" size="small" @click="editSaveDetail(scope.row)">保存</el-button>
									<el-button type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
								</div>
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn,format_money } from '../../config/mUtils'
	import { getInfoDistributeMotherCashApi,editSaveTotalDistributeMotherCashApi,editSaveDetailDistributeMotherCashApi,getEditLogDataDistributeMotherCashApi,editSaveLogDistributeMotherCashApi,getEventByDateApi,getRateApi } from '../../config/api'
	
	export default {
		inject: ['reload'],
		
		data() {
			return {
				id: '',
				rate: 1,// 实时汇率
				type: 1,//1代表母基金模块；2代表自管基金模块
				count: '',
				is_log_editable: false,
				is_total_editable: false,
				currentIndex: -1,
				
				total_selected_money: 0,
				total_selected_money_edit: 0,
				total_selected: [],
				total_selected_edit: [],
				
				relate_table_data: [],
				total_table_data: [],
				detail_table_data: [],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.count = this.$route.query.count;
			this.type = this.$route.query.type;
			this.getInitData()
		},
		computed: {

		},
		mounted() {},
		methods: {
			editLog(){
				this.is_log_editable = true;
				this.getEditLogData()
			},
			editTotal(){
				this.is_total_editable = true;
			},
			editDetail(row){
				this.currentIndex = row.id;
			},
			editCancel(){
				this.reload()
//				this.is_total_editable = false;
//				this.is_log_editable = false;
//				this.currentIndex = -1;
			},
			getInitData(){
				getInfoDistributeMotherCashApi({
					count: this.count,
					difference_sign: this.type
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.relate_table_data = res.data.payload.event_logs;
						this.total_table_data = res.data.payload.event;
						this.detail_table_data = res.data.payload.detail;
						this.handleSelectionChange(this.relate_table_data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editSaveTotal(e){
				editSaveTotalDistributeMotherCashApi({
					count: this.count,
					event: e.event,
					plan_receive_date: e.plan_receive_date,
					scale: e.scale,
					fof_id: this.id,
					id: e.id,
					difference_sign: this.type,
					event_count: e.event_count
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.is_total_editable = false;
						this.getInitData()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editSaveDetail(e){
				e['difference_sign'] = this.type;

				if(!e.receive_date){
					dialogConfirm("info", '实际到账日期不可为空');
					return;
				}
				editSaveDetailDistributeMotherCashApi(e).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.currentIndex = -1;
						this.getInitData()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getEditLogData(){
				getEditLogDataDistributeMotherCashApi({
					id: this.total_table_data[0].id,
					difference_sign: this.type
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.relate_table_data = res.data.payload.event;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editSaveLog(){
				var event_id = this.total_table_data[0].id;
				this.total_selected_edit.forEach(row => {
		            row['event_id'] = event_id;
		            row['payback_fof_id'] = row.id;
	          	});
				var event_log = JSON.stringify(this.total_selected_edit);
				editSaveLogDistributeMotherCashApi({
					count: this.count,
					event_log: event_log,
					difference_sign: this.type
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
//						this.is_log_editable = false;
//						this.getInitData()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			handleSelectionChange(e) {
				var total = 0;
				for (var i = 0,max = e.length;i<max;i++) {
					if (e.unit == 2) {
						total = total + Number(e[i].rest_fee) * rate;
					} else{
						total = total + Number(e[i].rest_fee);
					}
				}
				total = total.toFixed(6);
				total = format_money(total)
				if (this.is_log_editable) {
					this.total_selected_edit = e;
					this.total_selected_money_edit = total;
				} else{
					this.total_selected = e;
					this.total_selected_money = total;
				}
	      	},
	      	view(e){
	      		if (this.type == 1) {
	      			var name = 'motherFunRunDataCashFlowBackMoney'
	      		} else{
	      			var name = 'selfAdministerFunRunDataCashFlowBackMoney'
	      		}
	      		this.$router.push({
					name: name,
					query: {
						id: this.id,
						type: this.type,
						count: e.id,
					}
				})
	      	},
			calculate(index){
				// console.log(this.detail_table_data[index])
				if(parseFloat(this.detail_table_data[index].subscribe)< parseFloat(this.detail_table_data[index].principal_distribution)){
					dialogConfirm("info", '分配本金应小于或等于实缴出资!');
					this.detail_table_data[index].principal_distribution = 0;
				}
				// 计算单条记录的总额数据
				this.detail_table_data[index].this_sum_distribution = Number(this.detail_table_data[index].principal_distribution) + Number(this.detail_table_data[index].final_income)
				this.detail_table_data[index].sum_distribution = Number(this.detail_table_data[index].this_sum_distribution) + Number(this.detail_table_data[index].last_sum_distribution)
			},
			chooseDate(date){
				if (!date) {
					this.total_table_data[0].event = '';
					this.total_table_data[0].event_count = '';
					return
				}
				this.getEventCountData()
			},
			getEventCountData(){
				if (this.type == 1) {
					var type = 2;
				} else {
					var type = 3;
				}
				var date = this.total_table_data[0].plan_receive_date;
				var event_id = this.total_table_data[0].id;
				getEventByDateApi({
					type: type,
					id: this.id,
					cash_type: 4,
					date: date,
					fund_type: '',
					fund_id: '',
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.total_table_data[0].event = res.data.payload.event_name;
						this.total_table_data[0]['event_count'] = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getRate(){
				getRateApi({
					
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.rate = res.data.payload.exchangeRate
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 10px 30px 10px;
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right .delete {
		margin-right: 60px;
		color: #6292F4;
		font-size: 14px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	.cash_flow_table_title .blue_btn{
		margin-left: 20px;
	}
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
	
	/*新增样式*/
	.relete_back_title{
		margin-bottom: 28px;
		font-size: 14px;
		color: #6F7071;
	}
	.relete_back_title span{
		margin-left: 20px;
		font-size: 16px;
		color: #33302D;
	}
	.relete_back_title .blue_btn{
		width: 100px;
		margin-left: 20px;
		text-align: center;
	}
	.relete_back_title .hover_btn{
		width: 100px;
		text-align: center;
	}
	.submit_btn_box{
		margin: 35px 0 20px 0;
	}
	.submit_btn_box button{
		height: 36px;
		margin: 0 10px;
		line-height: 36px;
		padding: 0 25px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
</style>


